@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Didact+Gothic|Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i);
.navbar-wrapper {
  width: 100%;
  height: 80px;
  font-family: "Didact Gothic", sans-serif;
  border-bottom: 1px solid #a9b1d3; }
  .navbar-wrapper .navbar-logo {
    margin: 20px 0 0 0;
    float: left; }
  .navbar-wrapper .navbar-tabs {
    float: right; }
    .navbar-wrapper .navbar-tabs .navbar-tabitem {
      display: inline-block;
      margin: 30px 0 auto 30px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 300;
      color: #273c92; }

.content-wrapper {
  width: 100%; }
  .content-wrapper .title {
    margin: 10px 0;
    display: inline-block;
    font-family: "Didact Gothic", sans-serif;
    font-size: 24px;
    color: #273c92; }
  .content-wrapper .map {
    width: 100%;
    height: 600px;
    position: relative; }
  .content-wrapper .control {
    z-index: 1;
    position: absolute;
    left: 10px;
    top: 60px; }
  .content-wrapper .selector {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .content-wrapper .selector .handler {
      position: absolute;
      height: 12px;
      width: 12px;
      background-color: white;
      border: solid 2px #273c92;
      cursor: move; }
    .content-wrapper .selector .shader {
      background-color: rgba(0, 0, 0, 0.15); }
  .content-wrapper .proceed-button {
    margin: 18px 0;
    float: right; }
  .content-wrapper .proceed-alert {
    float: right;
    font-weight: 300;
    margin: 18px;
    line-height: 50px; }
    .content-wrapper .proceed-alert.red {
      color: red; }
    .content-wrapper .proceed-alert.green {
      color: green; }
  .content-wrapper .clearfix {
    clear: both; }
  .content-wrapper .row {
    width: 100%; }
  .content-wrapper .raw {
    padding: 24px 0;
    border-top: 1px solid #a9b1d3; }

body {
  margin: 0; }

.content {
  width: 83.3333%;
  margin: 0 auto;
  font-family: "Roboto", sans-serif; }

.button {
  display: inline-block;
  height: 50px;
  line-height: 50px;
  background-color: #273c92;
  padding: 0 20px;
  color: white;
  font-family: "Didact Gothic", sans-serif;
  font-size: 24px;
  text-decoration: none;
  transition: all .15s ease-in-out; }
  .button:hover {
    background-color: #5263a8; }
  .button.button-disabled {
    background-color: rgba(0, 0, 0, 0.4); }

